@import '../../../sass/variables.scss';

.container {
    //   margin-left: auto;
    //   margin-right: auto;
    //   width: 100%;
    margin-left: auto;
    margin-right: auto;

    // position: relative;
    // overflow-x: hidden;
    min-width: 320px;
    padding-left: 10px;
    padding-right: 10px;

    @media screen and (min-width: $tablet) {
        width: 768px;
        padding-left: 10px;
        padding-right: 10px;
    }

    @media screen and (min-width: $desktop) {
        width: 1240px;
        padding-left: 10px;
        padding-right: 10px;
    }
}